body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-hubballi: Hubballi;
  --font-ibm-plex-sans-condensed: "IBM Plex Sans Condensed";

  /* font sizes */
  --font-size-xs: 12px;

  /* Colors */
  --color-black: #000;
  --color-gray: rgba(255, 255, 255, 0.93);

  /* Border radiuses */
  --br-3xs: 10px;
  --br-31xl: 50px;
}
